      </v-col>
      <v-col cols="12" sm="4">
        <v-card
          class="pa-3"
          :class="{'mx-2':$vuetify.breakpoint.smAndUp,'my-2':$vuetify.breakpoint.smAndDown}"
        >
          <!-- <p class="subtitle-2">이동공유시설물 서비스</p> -->
          <v-card :ripple="{class:'white--text'}" class="blue darken-3">
            <!-- <v-img height="70" src="https://cdn.vuetifyjs.com/images/cards/server-room.jpg">
                  
            </v-img>-->
            <v-row align="center" class="fill-height ma-0 pa-0">
              <v-col class="ma-0 pa-0">
                <v-list-item color="rgba(0, 0, 0, .4)" dark>
                  <v-list-item-content>
                    <v-list-item-title class="title">이동공유시설물 서비스</v-list-item-title>
                    <v-list-item-subtitle>Mobile Sharing Service</v-list-item-subtitle>
                  </v-list-item-content>
