      </v-col>
      <v-col cols="12" sm="4">
        <v-card class="pa-3">
          <v-card :ripple="{class:'white--text'}">
            <!-- <v-img height="70" src="https://cdn.vuetifyjs.com/images/cards/server-room.jpg">
                  
            </v-img>-->
            <v-row align="center" class="fill-height ma-0 pa-0">
              <v-col class="ma-0 pa-0">
                <v-list-item color="rgba(0, 0, 0, .4)" dark>
                  <v-list-item-content>
                    <v-list-item-title class="title">네트워크 트래픽</v-list-item-title>
