<template>
  <v-row no-gutters class="mt-3">
    <v-col cols="12" sm="8" :class="{'pr-3':$vuetify.breakpoint.smAndUp}">
      <v-card
        id="map"
        style="width:100%"
        :height="$vuetify.breakpoint.smAndDown?280:467"
      >
      </v-card>
    </v-col>
    <v-col cols="12" sm="4" :class="{'mt-2':$vuetify.breakpoint.smAndDown}">
    
      <device-info :device="device"></device-info>
    </v-col>
  </v-row>
</template>
<script>
import { mapActions, mapMutations, mapState, mapGetters } from "vuex";
import { utils } from "@/mixins/utils";
import DeviceInfo from "@/components/DeviceInfo";
import constants from "../constants.js";
const BASE_URL = constants.BASE_URL;
const LORA_GW_IMG = BASE_URL + "/lora_gw.png";

export default {
  mixins: [utils],
  components: {
    DeviceInfo
  },
  data() {
    return {
      map: null,
      customOverlay: null,
      markers: [],
      device: null,
      lora_gw:[
        {
          name:"유성구청",
          lat:36.362263,
          lng: 127.356220
        },
        {
          name:"노은1동 동사무소",
          lat:36.368230,
          lng:127.318488
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["allDevice"])
  },
  mounted() {
    this.$nextTick(() => {
      var container = document.getElementById("map");
      var mapOptions = {
        center: new daum.maps.LatLng(36.375414, 127.401019),
        level: 8 //지도의 레벨(확대, 축소 정도)
      };
      this.map = new daum.maps.Map(container, mapOptions);
    });
  },
  methods: {
    removeAllMarker() {
      this.markers.forEach(marker => {
        marker.setMap(null);
      });
    },
    showOverlay(device) {
      var content =
        '<div class ="label"><span class="left"></span><span class="center">' +
        device.device_id +
        '</span><span class="right"></span></div>';

      // 커스텀 오버레이가 표시될 위치입니다
      var position = new kakao.maps.LatLng(device.lat, device.lng);

      if (this.customOverlay != null) {
        this.customOverlay.setMap(null);
      }

      // 커스텀 오버레이를 생성합니다
      var customOverlay = new kakao.maps.CustomOverlay({
        position: position,
        content: content,
        yAnchor: 1.5
      });
      this.customOverlay = customOverlay;
      customOverlay.setMap(this.map);
    }
  },
  watch: {
    allDevice() {
      if (this.map != null) {
        this.removeAllMarker();
        this.allDevice.forEach(device => {
          var imageSrc = this.getMarkerImage(device), // 마커이미지의 주소입니다
            imageSize = new kakao.maps.Size(15, 17), // 마커이미지의 크기입니다
            imageOption = {
              /* offset: new kakao.maps.Point(10, 12) */
            }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

          // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
          var markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imageOption
          );

          var marker = new kakao.maps.Marker({
            position: new daum.maps.LatLng(device.lat, device.lng),
            image: markerImage
          });

          daum.maps.event.addListener(marker, "click", mouseEvent => {
            this.device = device;
            var moveLatLon = new kakao.maps.LatLng(device.lat, device.lng);
            this.map.panTo(moveLatLon);
            this.showOverlay(device);
          });

          this.markers.push(marker);
        marker.setMap(this.map);
        });


        // lora 게이트웨이 고정 이미지
        this.lora_gw.forEach(gw => {

          var imageSrc = LORA_GW_IMG, // 마커이미지의 주소입니다
            imageSize = new kakao.maps.Size(20, 20), // 마커이미지의 크기입니다
            imageOption = {
              /* offset: new kakao.maps.Point(10, 12) */
            }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

          // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
          var markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imageOption
          );

          var marker = new kakao.maps.Marker({
            position: new daum.maps.LatLng(gw.lat, gw.lng),
            image: markerImage
          });

          
          this.markers.push(marker);
        marker.setMap(this.map);
        });
      }
    }
  }
};
</script>

<style scoped>
.fixed_height_300 {
  height: 120px;
}
</style>