<template>
  <v-container fluid style="margin: 0px; padding: 0px; width: 100%" class="pa-2">
    
    <v-select
      :items="items"
      hide-details
      item-text="name"
      class="mb-3"
      item-value="device_type"
      v-model="device_type"
      label="Device Type"
      style="width:280px"
      outlined
    ></v-select>
    <v-row no-gutters>
      <v-col cols="12" sm="4">
        <v-card class="pa-3 mr-2" :height="300">
          <p class="title">바이너리 파일 등록</p>
          <v-form ref="form">
            <v-file-input
              :rules="emptyRule"
              v-model="file"
              label="Binrary File"
              accept=".binary"
              outlined
              required
            ></v-file-input>

            <v-layout>
              <v-text-field
                ref="major"
                :prefix="'V'"
                :rules="emptyRule"
                label="Major version"
                :value="major_version | fillZero(2) "
                outlined
                class="mr-1"
                @keydown="e => setMajorVersion(e,major_version,2)"
                :maxlength="'2'"
                :counter="2"
                required
              ></v-text-field>
              <v-text-field
                ref="minor"
                :prefix="'.'"
                :rules="emptyRule"
                :value="minor_version | fillZero(4) "
                @keydown="e => setMinorVersion(e,minor_version,4)"
                label="Minor version"
                :counter="4"
                outlined
                required
              ></v-text-field>
            </v-layout>

            <v-layout>
              <v-spacer></v-spacer>
              <v-btn
                align-end
                justify-end
                text
                large
                outlined
                blue
                class="subtitle-1 blue white--text"
                @click="uploadFirmwareFile"
              >파일 업로드</v-btn>
            </v-layout>
          </v-form>
        </v-card>
      </v-col>

      <v-col cols="12" sm="8">
        <v-card :height="300">
          <v-data-table
            :fixed-header="true"
            :headers="firmware_headers"
            :height="300"
            :items="firmware_list"
            :hide-default-footer="true"
            :hide-default-header="$vuetify.breakpoint.smAndDown"
            :items-per-page="-1"
          >
            <template v-slot:body="{ items }">
              <tbody id="bb" ref="body">
                <tr v-for="item in items" :key="item.fileName">
                  <td class="text-center">{{ item.version }}</td>
                  <td class="text-center">{{ item.name }}</td>
                  <td class="text-center">{{ formatBytes(item.size) }}</td>
                  <td class="text-center">
                    <v-btn fab text small @click="showFileRemoveDialog(item)">
                      <v-icon small color="red">fas fa-trash-alt</v-icon>
                    </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-data-table>
          <v-dialog max-width="300" v-model="dialog">
            <v-card>
              <v-card-title class="headline">파일삭제</v-card-title>
              <v-card-text>{{remove_file!=null?remove_file.name:''}} 파일을 삭제하시겠습니까?</v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="white" text @click="dialog=false">취소</v-btn>
                <v-btn color="red" text @click="requestFileRemove">삭제</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-card>
      </v-col>
    </v-row>

    <v-row no-gutters>
      <v-col cols="12" sm="12">
        <v-card :height="650" class="pa-3 mt-2">
          <v-layout>
            <p class="title">장치별 펌웨어 정보</p>
            <v-spacer></v-spacer>

            <v-btn
              v-if="firmware_edit == false"
              align-end
              justify-end
              text
              outlined
              blue
              class="subtitle-1 blue white--text"
              @click="firmware_edit=true"
            >펌웨어 설정 변경</v-btn>

            <v-btn
              v-show="firmware_edit"
              align-end
              justify-end
              text
              @click="showFirmwareDialog"
              outlined
              blue
              class="subtitle-1 blue white--text mr-3"
            >설정</v-btn>

            <v-dialog :persistent="firmware_updating" v-model="firmware_dialog" width="650">
              <v-card>
                <v-card-title class="headline blue" primary-title>Firmware Setting</v-card-title>
                <v-progress-linear v-show="firmware_updating" indeterminate color="yellow darken-1"></v-progress-linear>
                <v-card-text>
                  <p class="title mt-5">Update Devices</p>
                  <v-chip
                    v-for="item in selected"
                    :key="item.device_id"
                    close
                    class="ma-1"
                    @click="alert(item.device_id)"
                    @click:close="remove(item)"
                  >
                    <strong>{{ item.device_id }}</strong>
                  </v-chip>
                  <p class="title mt-4">Select Firmware Version</p>

                  <v-select
                    :items="getFirmwareVersionList"
                    hide-details
                    class="ma-1"
                    v-model="firmware_version"
                    label="Firmware version"
                    outlined
                  ></v-select>
                  <p class="title mt-6">Firmware Update Enable</p>

                  <v-switch
                    color="primary"
                    v-model="firmware_enable"
                    :label="firmware_enable==true?'업데이트 활성화':'업데이트 비활성화'"
                  ></v-switch>
                </v-card-text>
                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" @click="updateFirmware" text>적용</v-btn>
                  <v-btn
                    color="white"
                    text
                    @click="firmware_updating==false?firmware_dialog = false:null"
                  >취소</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-btn
              v-if="firmware_edit == true"
              align-end
              justify-end
              text
              class="subtitle-1 red white--text"
              @click="firmware_edit=false"
            >취소</v-btn>
          </v-layout>
          <v-data-table
            v-model="selected"
            :fixed-header="true"
            :height="560"
            :headers="device_headers"
            :items="filterRow"
            :hide-default-footer="true"
            :hide-default-header="$vuetify.breakpoint.smAndDown"
            :items-per-page="-1"
            item-key="device_id"
            :show-select="firmware_edit"
            class="mt-1"
            dense
          >
            <template v-slot:body="{ items }">
              <tbody id="bb" ref="body">
                <tr v-for="item in items" :key="item.device_id">
                  <td v-if="firmware_edit">
                    <v-checkbox
                      v-model="selected"
                      :value="item"
                      style="margin:0px;padding:0px"
                      hide-details
                    />
                  </td>
                  <td
                    class="text-center"
                    :class="{'fixed_width_30': $vuetify.breakpoint.smAndDown,'caption': $vuetify.breakpoint.smAndDown}"
                  >{{ item.device_id }}</td>
                  <td class="text-center" :class="{'caption': $vuetify.breakpoint.smAndDown }">
                    <v-chip
                      class="my-1"
                      :color="getColor(item.status)"
                      dark
                      :class="{'caption': $vuetify.breakpoint.smAndDown}"
                    >{{ item.status | statusToString }}</v-chip>
                  </td>
                  <td
                    class="text-center"
                    :class="{'caption': $vuetify.breakpoint.smAndDown}"
                  >{{ item.timestamp }}</td>
                  <td
                    class="text-center"
                    :class="{'caption': $vuetify.breakpoint.smAndDown}"
                  >{{ item.current_version }}</td>
                  <td
                    class="text-center"
                    :class="{'caption': $vuetify.breakpoint.smAndDown}"
                  >{{ item.update_version }}</td>
                  <td
                    class="text-center"
                    :class="{'caption': $vuetify.breakpoint.smAndDown,'green--text':item.update_enable=='true','red--text':item.update_enable=='false'}"
                  >{{ item.update_enable | enableToString }}</td>
                </tr>
              </tbody>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <!--  accept=".binary"  -->
  </v-container>
</template>
<script>
import axios from "axios";
export default {
  created() {},
  data: () => ({
    major_version: "",
    minor_version: "",
    firmware_updating: false,
    firmware_enable: true,
    firmware_version: null,
    firmware_edit: false,
    device_list: [],
    remove_file: null,
    dialogNote: {},
    dialog: false,
    firmware_dialog: false,
    version: "",
    file: null,
    emptyRule: [v => !!v || "value is required"],
    items: [
      { name: "고정형(C)", device_type: 1 },
      { name: "고정형(B)", device_type: 2 },
      { name: "고정형(케이스)", device_type: 3 },
      { name: "이동형(케이벨)", device_type: 4 },
      { name: "이동형(타슈)", device_type: 5 }
    ],
    firmware_list: [],
    device_type: 0,
    firmware_headers: [
      {
        text: "버전",
        align: "center",
        filterable: true,
        value: "version"
      },
      {
        text: "파일명",
        align: "center",
        sortable: false,
        filterable: true,
        value: "name"
      },
      {
        text: "파일크기",
        align: "center",
        filterable: true,
        value: "size"
      },
      {
        text: "파일삭제",
        align: "center",
        sortable: false,
        filterable: true,
        value: "size"
      }
    ],
    selected: [],
    device_headers: [
      {
        text: "장치 아이디",
        align: "center",
        filterable: true,
        value: "device_id"
      },

      {
        text: "장치 상태",
        align: "center",
        filterable: true,
        value: "status"
      },
      {
        text: "최근 데이터 수신시간",
        align: "center",
        filterable: true,
        value: "timestamp"
      },
      {
        text: "현재 버전",
        align: "center",
        filterable: true,
        value: "current_version"
      },
      {
        text: "업데이트 버전",
        align: "center",
        filterable: true,
        value: "update_version"
      },
      {
        text: "업데이트 활성화",
        align: "center",
        filterable: true,                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          
        value: "update_version"
      }
    ]
  }),
  methods: {
    setMinorVersion(e, data, limit) {
      e.preventDefault(); // 이벤트 전달을 차단

      if (
        (e.keyCode >= 48 && e.keyCode <= 57) ||
        (e.keyCode >= 96 && e.keyCode <= 105) // key pad numbers
      ) {
        if (this.minor_version.length < limit) {
          this.minor_version += e.key;
        }
      }

      if (e.keyCode == 8) {
        // backspace
        if (this.minor_version.length > 0) {
          this.minor_version = this.minor_version.substring(
            0,
            this.minor_version.length - 1
          );
        }
      }
    },
    setMajorVersion(e, data, limit) {
      e.preventDefault();

      if (
        (e.keyCode >= 48 && e.keyCode <= 57) ||
        (e.keyCode >= 96 && e.keyCode <= 105)
      ) {
        if (this.major_version.length < limit) {
          this.major_version += e.key;
        }
      }

      if (e.keyCode == 8) {
        if (this.major_version.length > 0) {
          this.major_version = this.major_version.substring(
            0,
            this.major_version.length - 1
          );
        }
      }
    },
    remove(item) {
      this.selected.splice(this.selected.indexOf(item), 1);
      this.selected = [...this.selected];
    },
    showFileRemoveDialog(item) {
      for (var i = 0; i < this.filterRow.length; i++) {
        if (this.filterRow[i].update_version == item.version) {
          alert(
            "해당 업데이트 버전을 사용중인 장치가 존재합니다 기존 장치의 업데이트 버전을 다른 버전으로 바꿔주세요"
          );
          return;
        }
      }

      this.remove_file = item;
      this.dialog = true;
    },
    requestFileRemove() {
      axios
        .get("/api/firmware_remove", {
          params: {
            device_type: this.device_type,
            name: this.remove_file.name
          }
        })
        .then(res => {
          if (res.data) {
            this.dialog = false;
            this.getFirmwareList();
            // alert("삭제완료");
          } else {
            alert("삭제실패");
          }
        })
        .catch(error => {
          alert(error);
        });
    },
    getDeviceList() {
      axios.get("/api/device_list").then(res => {
        this.device_list = res.data;
        this.selected = [];
      });
    },

    getColor(status) {
      if (status == 0) return "white";
      else if (status == 1) return "light-green";
      else if (status == 2) return "red";
      else return "orange";
    },
    formatBytes(bytes, decimals = 1) {
      if (bytes === 0) return "0 Bytes";
      const k = 1024;
      const dm = decimals < 0 ? 0 : decimals;
      const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

      const i = Math.floor(Math.log(bytes) / Math.log(k));

      return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
    },

    getFirmwareList() {
      axios
        .get("/api/firmware_list", {
          params: {
            device_type: this.device_type
          }
        })
        .then(res => {
          this.firmware_list = res.data;
        })
        .catch(error => {
          this.firmware_list = [];
          alert(error);
        });
    },

    uploadFirmwareFile() {
      let formData = new FormData();

      if (this.device_type == 0) {
        alert("장치타입을 선택해주세요");
        return;
      }

      if (this.$refs.form.validate()) {
        formData.append("device_type", this.device_type);
        formData.append(
          "version",
          this.$refs.major.value + "." + this.$refs.minor.value
        );
        //formData.append("version", this.version);
        formData.append("binary_file", this.file);

        axios
          .post("/api/firmware_upload", formData, {
            headers: { "Content-Type": "multipart/form-data" }
          })
          .then(response => {
            if (response.data) {
              alert("업로드 성공");
              this.getFirmwareList();
              this.file = null;
              //this.version = "";
              this.major_version = "";
              this.minor_version = "";
              this.$refs.form.resetValidation();
            } else {
              alert(
                "업로드 실패 (펌웨어 버전을 기존버전 보다 높게 입력해주세요)"
              );
            }
          })
          .catch(error => {
            alert(error);
            this.file = null;
            //this.version = "";
            this.major_version = "";
            this.minor_version = "";
            this.$refs.form.resetValidation();
          });
      }
    },
    showFirmwareDialog() {
      if (this.selected.length > 0) {
        this.firmware_version = null;
        this.firmware_dialog = true;
      } else {
        alert("장치를 선택해주세요");
      }
    },
    updateFirmware() {
      if (this.selected == 0) {
        alert("장치를 선택해주세요");
        return;
      }

      if (this.firmware_version == null) {
        alert("펌웨어 버전을 선택해주세요");
        return;
      }

      this.firmware_updating = true;

      axios
        .post("/api/firmware_update", null, {
          params: {
            device_list: this.getSeletedDeviceIds,
            version: this.firmware_version,
            firmware_enable: this.firmware_enable
          }
        })
        .then(res => {
          this.firmware_dialog = false;
          this.firmware_updating = false;
          this.selected = [];
          this.getDeviceList();
        })
        .catch(error => {
          this.firmware_dialog = false;
          this.firmware_updating = false;
          this.selected = [];
          alert(error);
        });
    }
  },
  watch: {
    device_type() {
      this.getFirmwareList();
      this.getDeviceList();
    }
  },
  computed: {
    minorVersion: {
      get() {
        var limit = 4;
        var result = "";
        for (var i = 0; i < limit - this.minor_version.length; i++) {
          result += "0";
        }

        return result + this.minor_version;
      },
      set(newValue) {
        this.minor_version = newValue;
      }
    },
    filterRow() {
      return this.device_list.filter(item => {
        return this.device_type > 0 && item.device_type == this.device_type;
      });
    },
    getSeletedDeviceIds() {
      var ids = [];
      this.selected.forEach(item => {
        ids.push(item.device_id);
      });
      return ids;
    },
    getFirmwareVersionList() {
      var version_list = [];
      this.firmware_list.forEach(item => {
        version_list.push(item.version);
      });
      return version_list;
    }
  },
  filters: {
    fillZero: function(value, limit) {
      return value.padStart(limit, "0");
    },
    typeToString: function(value) {
      if (value == 1) {
        return "고정형";
      } else {
        return "이동형";
      }
    },
    statusToString: function(value) {
      switch (value) {
        case 1:
          return "정상";
        case 2:
          return "수신불량";
        case 3:
          return "배터리부족";
        default:
          return "-";
      }
    },
    enableToString: function(enable) {
      if (enable === "true") {
        return "활성";
      } else {
        return "비활성";
      }
    }
  }
};
</script>
