    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-center" width="15%">모듈</th>
            <th class="text-center" width="15%">갯수</th>
            <th class="text-center" width="30%">Rx(recent)</th>
            <th class="text-center" width="30%">Tx(recent)</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td class="text-center">{{ item.name }}</td>

            <td class="text-center" v-if="item.name == 'LTE'">{{ deviceCount.lte }}대</td>
            <td class="text-center" v-else-if="item.name == 'LoRa'">{{ deviceCount.lora }}대</td>
            <td class="text-center" v-else>-</td>

            <td class="text-center" v-if="item.name == 'LTE'">
              {{ lteModuleTrafficValue!= null? formatBytes(lteModuleTrafficValue.total_rx_traffic_byte):'-' }}
               (<span class="red--text">{{lteModuleTrafficValue!=null?formatBytes(lteModuleTrafficValue.increase_rx_traffic_byte)+'▲':'-'}}</span>)
            </td>
             <td class="text-center" v-else-if="item.name == 'LoRa'">
              {{ loraModuleTrafficValue!= null? formatBytes(loraModuleTrafficValue.total_rx_traffic_byte):'-' }}
               (<span class="red--text">{{loraModuleTrafficValue!=null?formatBytes(loraModuleTrafficValue.increase_rx_traffic_byte)+'▲':'-'}}</span>)
            </td>
            <td class="text-center" v-else>-</td>

            <td class="text-center" v-if="item.name == 'LTE'">
