               (<span class="red--text">{{lteModuleTrafficValue!=null?formatBytes(lteModuleTrafficValue.increase_tx_traffic_byte)+'▲':'-'}}</span>)
            </td>
              <td class="text-center" v-if="item.name == 'LoRa'">
              {{ loraModuleTrafficValue!= null? formatBytes(loraModuleTrafficValue.total_tx_traffic_byte):'-' }}
               (<span class="red--text">{{loraModuleTrafficValue!=null?formatBytes(loraModuleTrafficValue.increase_tx_traffic_byte)+'▲':'-'}}</span>)
            </td>
            <td class="text-center" v-else>-</td>
