import Vue from 'vue'
import Vuex from 'vuex'

const LTE_RX_BYTE = 85; // 센서데이터 수신시 byte수
const LTE_TX_BYTE = 6; // 센서데이터 수신시 응답 byte수 
const LORA_RX_BYTE = 39; // 센서데이터 수신시 byte수
const LORA_TX_BYTE = 0; // 센서데이터 수신시 응답 byte수 

const DB_TYPE_MYSQL = 1; // 센서데이터 수신시 byte수
const DB_TYPE_ELASTICSEARCH = 2; // 센서데이터 수신시 byte수

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    device_list: [], //lte
    lora_list: [], // lora
    lte_module_traffic: [],
    lora_module_traffic: [],
    elk: [],
    mysql: [],
  },
  getters: {
    allDevice: state => {
      return state.device_list
    },
    allLoraDevice: state => {
      return state.lora_list
    },
    deviceCount: state => {

      return {
        lte: state.device_list.length,
        lora: state.lora_list.length
      }
    },
    lteModuleTrafficValue: (state, getters) => {
      var list = state.lte_module_traffic;
      if (list.length > 0) {
        return {
          total_rx_traffic_byte: list[list.length - 1].total_count * LTE_RX_BYTE,
          increase_rx_traffic_byte: list[list.length - 1].add_count * LTE_RX_BYTE,
          total_tx_traffic_byte: list[list.length - 1].total_count * LTE_TX_BYTE,
          increase_tx_traffic_byte: list[list.length - 1].add_count * LTE_TX_BYTE,
        }
      } else {
        return null;
      }
    },
    loraModuleTrafficValue: (state, getters) => {
      var list = state.lora_module_traffic;
      if (list.length > 0) {
        return {
          total_rx_traffic_byte: list[list.length - 1].total_count * LORA_RX_BYTE,
          increase_rx_traffic_byte: list[list.length - 1].add_count * LORA_RX_BYTE,
          total_tx_traffic_byte: list[list.length - 1].total_count * LORA_TX_BYTE,
          increase_tx_traffic_byte: list[list.length - 1].add_count * LORA_TX_BYTE,
        }
      } else {
        return null;
      }
    },
    moduleTrafficChartData: (state, getters) => {
      var labels = [];
      var lte_datas = [];
      var lora_datas = [];
      state.lte_module_traffic.forEach(item => {
        labels.push(item.time_stamp)
        lte_datas.push(item.add_count)
      });

      state.lora_module_traffic.forEach(item => {
        lora_datas.push(item.add_count)
      });


      return {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          title: {
            display: true,
            text: "Module Packet Rx Count "
          },
          legend: {
            position: "bottom"
          },
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true,
                padding: 25,
              }
            }]
          }
        },
        chartdata: {
          labels: labels,
          datasets: [
            {
              label: "LTE ",
              backgroundColor: "rgba(255, 85, 103, 0.4)",
              data: lte_datas,
              pointRadius: 0,
              // pointHoverRadius: 0
            },
            {
              label: "LORA",
              backgroundColor: "rgba(58, 139, 197, 0.5)",
              data: lora_datas,
              pointRadius: 0,
              // pointHoverRadius: 0
            }
          ]
        }
      }
    },
    dbElkInfo: state => {
      return state.elk
    },
    dbElkInfoChartData: (state, getters) => {
      var labels = [];
      var conCountData = [];
      var transcationCountData = [];

      getters.dbElkInfo.forEach(item => {
        labels.push(item.time_stamp)
        conCountData.push(item.increase_con_count)
        transcationCountData.push(item.increase_transaction_count)
      });

      return {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          title: {
            display: true,
            text: "Elastic Search Resource Usage"
          },
          legend: {
            position: "bottom"
          },
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true,
                padding: 25,
              }
            }]
          }
        },
        chartdata: {
          labels: labels,
          datasets: [
            {
              label: "Connection Count",
              backgroundColor: "rgba(255, 85, 103, 0.4)",
              data: conCountData,
              pointRadius: 0,
              pointHoverRadius: 0
            },
            {
              label: "Transcation Count",
              backgroundColor: "rgba(58, 139, 197, 0.4)",
              data: transcationCountData,
              pointRadius: 0,
              pointHoverRadius: 0
            }
          ]
        }
      }
    },
    dbElkInfoValue: (state, getters) => {
      var list = getters.dbElkInfo;
      if (list.length > 0) {
        return list[list.length - 1];
      } else {
        return null;
      }
    },
    dbMysqlInfo: state => {
      return state.mysql
    },
    dbMysqlInfoChartData: (state, getters) => {
      var labels = [];
      var conCountData = [];
      var transcationCountData = [];

      getters.dbMysqlInfo.forEach(item => {
        labels.push(item.time_stamp)
        conCountData.push(item.increase_con_count)
        transcationCountData.push(item.increase_transaction_count)
      });

      return {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          title: {
            display: true,
            text: "Mysql Resource Usage"
          },
          legend: {
            position: "bottom"
          },
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true,
                padding: 25,
              }
            }]
          }
        },
        chartdata: {
          labels: labels,
          datasets: [
            {
              label: "Connection Count",
              backgroundColor: "rgba(255, 85, 103, 0.4)",
              data: conCountData,
              pointRadius: 0,
              pointHoverRadius: 0
            },
            {
              label: "Transcation Count",
              backgroundColor: "rgba(58, 139, 197, 0.4)",
              data: transcationCountData,
              pointRadius: 0,
              pointHoverRadius: 0
            }
          ]
        }
      }
    },
    dbMysqlInfoValue: (state, getters) => {
      var list = getters.dbMysqlInfo;
      if (list.length > 0) {
        return list[list.length - 1];
      } else {
        return null;
      }
    }

  },
  mutations: {
    // 값을 변화시키는 곳
    // addUsers: (state, payload) => {
    //   state.users.push(payload)
    // }
  },
  actions: {
    // 값을 처리하는 곳
    // addUsers: ({ commit }, payload) => {  // context.commit = ({commit}) 객체형태로 불러옴
    //   commit('addUsers', payload)
    // }
  },
  modules: {

  }
})
