<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  props: ["mChartData"],
  data: () => ({
  }),
  methods: {},
  created() {
  },
  watch: {
    mChartData() {
      //this.renderChart(this.mChartData.chartdata, this.mChartData.options);

      if (this.$data._chart == null) {
        this.renderChart(this.mChartData.chartdata, this.mChartData.options);
      }

      this.$data._chart.config.data.labels = this.mChartData.chartdata.labels;
      this.$data._chart.config.data.datasets.forEach((item, index) => {
        item.data = this.mChartData.chartdata.datasets[index].data;
      });
      this.$data._chart.update();
    }
  }
};
</script>