          </v-col>
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">수신불량</p>
            <devices-dialog :filter_type="0" :filter_status="2" :datas="allDevice"></devices-dialog>
          </v-col>
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">충전필요</p>
            <devices-dialog :filter_type="0" :filter_status="3" :datas="allDevice"></devices-dialog>
          </v-col>
