    </v-col>
    <v-col cols="12" sm="4">
      <v-card :class="{'mx-3':$vuetify.breakpoint.smAndUp,'my-2':$vuetify.breakpoint.smAndDown}">
        <p
          class="blue white--text pa-2 ma-0 pl-5"
          style="
          background: #3A1C71;
