          background: linear-gradient(to right, #3F51B5 , #2196F3);"
        >고정형 디바이스</p>

        <v-row class="px-3">
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">전체</p>
            <devices-dialog :filter_type="1" :filter_status="0" :datas="allDevice"></devices-dialog>
          </v-col>
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">정상</p>
            <devices-dialog :filter_type="1" :filter_status="1" :datas="allDevice"></devices-dialog>
          </v-col>
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">수신불량</p>
            <devices-dialog :filter_type="1" :filter_status="2" :datas="allDevice"></devices-dialog>
