            <devices-dialog :filter_type="2" :filter_status="2" :datas="allDevice"></devices-dialog>
          </v-col>
          <v-col sm="3" style="text-align:center">
            <p class="subtitle-1 mb-2">충전필요</p>
            <devices-dialog :filter_type="2" :filter_status="3" :datas="allDevice"></devices-dialog>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import DevicesDialog from "@/components/DevicesDialog";
import { mapActions, mapMutations, mapState, mapGetters } from "vuex";
const FIXED_DEVICE = 1;
const MOVEMENT_DEVICE = 2;
export default {
  components: {
    DevicesDialog
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["allDevice"]),
    allDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      let battery = 0;
      this.allDevice.forEach(item => {
        if (item.status == 1) normal++;
        if (item.status == 2) broken++;
        if (item.status == 3) battery++;
        total++;
      });

      return {
        total: total,
        normal: normal,
        broken: broken,
        battery: battery
      };
    },
    movementDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      let battery = 0;
      this.allDevice.forEach(item => {
        if (item.type == MOVEMENT_DEVICE) {
          if (item.status == 1) normal++;
          if (item.status == 2) broken++;
          if (item.status == 3) battery++;
          total++;
        }
      });

      return {
        total: total,
        normal: normal,
        broken: broken,
        battery: battery
      };
    },
    fixedDeviceInfo() {
      let total = 0;
      let normal = 0;
      let broken = 0;
      let battery = 0;
      this.allDevice.forEach(item => {
        if (item.type == FIXED_DEVICE) {
          if (item.status == 1) normal++;
          if (item.status == 2) broken++;
          if (item.status == 3) battery++;
          total++;
        }
      });

      return {
        total: total,
        normal: normal,
        broken: broken,
        battery: battery
      };
    }
  },
  methods: {}
};
</script>