<template>
  <div>
    <v-dialog v-model="dialog" fullscreen hide-overlay>
      <template v-slot:activator="{ on }">
        <v-btn
          fab
          text
          large
          outlined
          class="title"
          v-on="on"
          :class="{
             'white--text': filter_status ==0,
             'light-green--text':filter_status == 1 && filter.length >0,
             'red--text': filter_status == 2 && filter.length >0,
             'yellow--text': filter_status == 3 && filter.length >0}"
        >{{filter.length}}</v-btn>
      </template>
      <v-card>
        <v-card-title class="pr-2">
          장치정보
          <v-spacer></v-spacer>
          <v-select
            v-if="filter_type==0"
            v-model="select"
            :items="select_items"
            item-text="type"
            class="pb-5 mr-3"
            item-value="value"
            label="Select"
            hide-details
            style="width:100px"
            return-object
            single-line
          ></v-select>
          <v-btn color="blue darken-1" text @click="dialog = false" class>Close</v-btn>
        </v-card-title>

        <v-divider class="my-1"></v-divider>
        <v-card-text class="px-3">
          <v-row>
            <v-col cols="12" sm="5" class="ma-0 pa-0">
              <div
                id="map"
                style="width:100%"
                :class="{'fixed_height_300':$vuetify.breakpoint.smAndDown,' fill-height':$vuetify.breakpoint.smAndUp}"
              ></div>
            </v-col>
            <v-col cols="12" sm="7" class="pa-0">
              
              <v-data-table
                :fixed-header="true"
                :headers="headers"
                :height="$vuetify.breakpoint.smAndDown?480:870"
                :items="filterRow"
                :hide-default-footer="true"
                :hide-default-header="$vuetify.breakpoint.smAndDown"
                :search="search"
                :items-per-page="-1"
                item-key="device_id"
                class="mt-1"
                dense
              >
                <template v-slot:body="{ items }">
                  <tbody id="bb" ref="body">
                    <tr
                      v-for="item in items"
                      :key="item.device_id"
                      :class="{blue:item.device_id == selected_device_id}"
                      @click="selectRow(item)"
                      @dblclick="findPath(item)"
                    >
                      <td
                        class="text-center"
                        :class="{'fixed_width_30': $vuetify.breakpoint.smAndDown,'caption': $vuetify.breakpoint.smAndDown}"
                      >{{ item.device_id }}</td>
                      <td
                        class="text-center"
                        :class="{'caption': $vuetify.breakpoint.smAndDown}"
                      >{{ item.timestamp }}</td>
                      <td
                        class="text-center"
                        :class="{'caption': $vuetify.breakpoint.smAndDown}"
                      >{{ item.address }}</td>
                      <td
                        class="text-center"
                        :class="{'caption': $vuetify.breakpoint.smAndDown , 'd-none': $vuetify.breakpoint.smAndDown}"
                      >{{ item.battery }}%</td>
                      <td
                        class="text-center"
                        :class="{'caption': $vuetify.breakpoint.smAndDown , 'd-none': $vuetify.breakpoint.smAndDown}"
                      >{{ item.type | typeToString }}</td>
                      <td class="text-center" :class="{'caption': $vuetify.breakpoint.smAndDown }">
                        <v-chip
                          class="my-1"
                          :color="getColor(item.status)"
                          dark
                          :class="{'caption': $vuetify.breakpoint.smAndDown}"
                        >{{ item.status | statusToString }}</v-chip>
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-data-table>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<style>
.label {
  margin-bottom: 3px;
  color: black;
}
.label * {
  display: inline-block;
  vertical-align: top;
}
.label .left {
  background: url("http://t1.daumcdn.net/localimg/localimages/07/2011/map/storeview/tip_l.png")
    no-repeat;
  display: inline-block;
  height: 24px;
  overflow: hidden;
  vertical-align: top;
  width: 7px;
}
.label .center {
  background: url(http://t1.daumcdn.net/localimg/localimages/07/2011/map/storeview/tip_bg.png)
    repeat-x;
  display: inline-block;
  height: 24px;
  font-size: 12px;
  line-height: 24px;
}
.label .right {
  background: url("http://t1.daumcdn.net/localimg/localimages/07/2011/map/storeview/tip_r.png") -1px
    0 no-repeat;
  display: inline-block;
  height: 24px;
  overflow: hidden;
  width: 6px;
}

.fixed_height_300 {
  height: 250px;
}

.fixed_width_30 {
  width: 30px;
}
</style>
<script>
import { utils } from "@/mixins/utils";
export default {
  mixins: [utils],
  props: ["filter_type", "filter_status", "datas"],
  data() {
    return {
      select: { type: "전체", value: 0 },
      select_items: [
        { type: "전체", value: 0 },
        { type: "A타입", value: 3 },
        { type: "B타입", value: 2 },
        { type: "C타입", value: 1 },
        { type: "타슈", value: 4 }
      ],
      dialog: false,
      customOverlay: null,
      selected_device_id: -1,
      markers: [],
      map: null,
      search: "",
      headers: [
        {
          text: "장치 아이디",
          align: "center",
          filterable: true,
          value: "device_id"
        },
        {
          text: "데이터 수신시간",
          align: "center",
          filterable: true,
          value: "timestamp"
        },
        {
          text: "위치",
          align: "center",
          filterable: true,
          value: "address"
        },
        {
          text: "배터리",
          align: "center",
          filterable: true,
          value: "battery"
        },
        {
          text: "장치타입",
          align: "center",
          filterable: true,
          value: "type"
        },
        {
          text: "상태",
          align: "center",
          filterable: true,
          value: "status"
        }
      ],
      desserts: []
    };
  },
  filters: {
    typeToString: function(value) {
      if (value == 1) {
        return "고정형";
      } else {
        return "이동형";
      }
    },
    statusToString: function(value) {
      switch (value) {
        case 1:
          return "정상";
        case 2:
          return "수신불량";
        case 3:
          return "배터리부족";
        default:
          return "-";
      }
    }
  },
  computed: {
    filter() {
      return this.datas.filter(item => {
        var result = false;
        // item.type = 1 : 고정형 , 2: 이동형 // 0 : 전체
        // item.status = 1 : 정상 , 2: 수신불량 , 3: 배터리부족  // 0 : 전체
        if (this.filter_type === 0) {
          result =
            this.filter_status === 0
              ? true
              : this.filter_status === item.status;
        } else {
          if (this.filter_type === item.type) {
            result =
              this.filter_status === 0
                ? true
                : this.filter_status === item.status;
          }
        }

        return result;
      });
    },
    filterRow() {
      return this.filter.filter(item => {
        if (this.select.value == 0) {
          return true;
        } else {
          var t = parseInt(item.device_id / 10000);
          return t == this.select.value;
        }
      });
    }
  },
  watch: {
    dialog() {
      if (this.dialog) {
        this.$nextTick(() => {
          var container = document.getElementById("map");
          var mapOptions = {
            center: new daum.maps.LatLng(36.375414, 127.401019),
            level: 8 //지도의 레벨(확대, 축소 정도)
          };

          if (this.map == null) {
            this.map = new daum.maps.Map(container, mapOptions);
          }

          this.addMarker();
        });
      }
    },
    filterRow(){
       this.addMarker(); // 필터(전체, A,B,C,타슈)에 따른 마커 초기화
    }
  },
  methods: {
    findPath(device){
   window.open("https://map.kakao.com/link/to/"+device.address+","+device.lat+","+device.lng)

    },
    selectRow(device) {
   
      this.selected_device_id = device.device_id;
      var moveLatLon = new kakao.maps.LatLng(device.lat, device.lng);
      //this.map.setLevel(3);
      this.map.panTo(moveLatLon);
      this.showOverlay(device);
    },
    getColor(status) {
      if (status == 0) return "white";
      else if (status == 1) return "light-green";
      else if (status == 2) return "red";
      else return "orange";
    },
    showOverlay(device) {
      var content =
        '<div class ="label"><span class="left"></span><span class="center">' +
        device.device_id +
        '</span><span class="right"></span></div>';

      // 커스텀 오버레이가 표시될 위치입니다
      var position = new kakao.maps.LatLng(device.lat, device.lng);

      if (this.customOverlay != null) {
        this.customOverlay.setMap(null);
      }

      // 커스텀 오버레이를 생성합니다
      var customOverlay = new kakao.maps.CustomOverlay({
        position: position,
        content: content,
        yAnchor: 1.5
      });
      this.customOverlay = customOverlay;
      customOverlay.setMap(this.map);
    },
    addMarker() {

      if (this.customOverlay != null) {
        this.customOverlay.setMap(null);
      }

      if (this.markers.length > 0) {
        this.markers.forEach(marker => {
          marker.setMap(null);
        });
      }

      this.filterRow.forEach(device => {
        var imageSrc = this.getMarkerImage(device), // 마커이미지의 주소입니다
          imageSize = new kakao.maps.Size(15, 17), // 마커이미지의 크기입니다
          imageOption = {
            /* offset: new kakao.maps.Point(10, 12) */
          }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

        // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
        var markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imageOption
        );

        var marker = new kakao.maps.Marker({
          position: new daum.maps.LatLng(device.lat, device.lng),
          image: markerImage
        });

        daum.maps.event.addListener(marker, "click", mouseEvent => {
          this.selected_device_id = device.device_id;
          var moveLatLon = new kakao.maps.LatLng(device.lat, device.lng);
          this.map.panTo(moveLatLon);
          this.showOverlay(device);
        });

        this.markers.push(marker);
        marker.setMap(this.map);
      });
    }
  }
};
</script>
